<template>
    <div>
      <div class="sign-top">
        <div class="container">
          <div class="link">
            <ul>
              <li ><a href="/login"></a></li>
              <li class="on"><a href="/reg" @click.prevent="prevent()"></a><span class="slider"></span></li>
            </ul>
          </div>
          <div class="logo">
            <a href="/">
              <img src="/static/public/images/header-logo.jpg" alt="" width="200" height="110px">
            </a>
          </div>
        </div>
      </div>
      <div class="sign-body register" style="padding-top: 60px;">
        <div class="container clearfix" style="height: 400px;">
          <div class="ad">
            <a href="#" style="background: url(/static/public/images/ad.jpg) center no-repeat;background-size: cover;"></a>
          </div>
          <div class="formBox">
            <h2></h2>
            <form action="/reg" id="register" method="post">
              <ul>
                <li>
                  <input type="text" class="input-box" name="userName" id="user_name" placeholder="" v-model="user_name">
                  <p class="error-msg"></p>
                </li>
                <li>
                  <input type="password" class="input-box" name="loginPwd" placeholder="" v-model="user_pwd">
                  <p class="error-msg"></p>
                </li>
                <li>
                  <input type="text" class="input-box"  id="real_name" placeholder="" v-model="real_name">
                  <p class="error-msg"></p>
                </li>
                <li>
                  <input type="text" class="input-box"  placeholder="" v-model="user_num">
                  <p class="error-msg"></p>
                </li>


                <li>
                  <input type="submit" class="btn-large" id="regist_btn" value="" @click.prevent="reg()">
                </li>
                <li class="option">
                  ?<a href="/login" class="sublink"></a>
                </li>
              </ul>
            </form>
          </div>
        </div>
        <div class="footer navbar-fixed-bottom">
          Copyright&copy;2019-2028     
        </div>
      </div>
    </div>
</template>

<script>
  import {pwdReg} from './../../api/index'
  import swal from 'sweetalert';
    export default {
        name: "Reg",
        data(){
          return{
                user_pwd:'',//
                user_name:'',//
                userInfo:{},//
                real_name:'',//
                user_num:''//
          }
        },
        methods:{
          //
          async reg(){
            //3.1
            if(!this.user_name){
              swal("");
              return;
            }else if(!this.user_pwd){
              swal("");
              return;
            }else if(!this.real_name){
              swal("");
              return;
            }else if(!this.user_num){
              swal("");
              return;
            }
            // console.log(this.user_name);

            //3.2
            const result = await pwdReg(this.user_name,this.user_pwd,this.real_name,this.user_num);
            console.log(result);
            //
            if(result.success_code === 200){
              this.userInfo = result.message;
              swal('');
              this.$router.push({path:'/home'});
            }
            else{
              swal("");
              this.user_name='';
              this.user_pwd='';
              this.real_name='';
              this.user_num='';
            }

          }
        }
    }
</script>

<style scoped>

</style>



